.App {
  text-align: center;
}

.App-header {
  background-color: #454f62;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin: 0;
}

img {
  width: 200px;
  height: auto;
}

.card {
  border: 1px solid black;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.container > * {
  flex-basis: calc(17% - 16px);
  margin: 8px;
}

.filterContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.filterContainer > * {
  margin: 4px 20px;
}

button {
  font-family: "Lucida Console", "Courier New", monospace;
  background-color: #282c34;
  color: white;
  padding: 5px 15px;
  border: 1px solid white;
}

button:hover {
  background-color: #485876;
}

/* ----------------------------------------------- */
/* The following is to style a menu bar */
/* ----------------------------------------------- */
ul.menu {
  display: flex;
  justify-content: center;
  font-family: "Lucida Console", "Courier New", monospace;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #282c34;
}

ul.menu li {
  border-right: 1px solid #bbb;
}

ul.menu li:last-child {
  border-right: none;
}

ul.menu li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
}

ul.menu li a:hover:not(.active) {
  background-color: #485876;
}

ul.menu .active {
  background-color: red;
}
/* ----------------------------------------------- */

.center {
  height: 80vmin;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-logo {
  height: 60vmin;
  width: auto;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
